import { EnvService } from 'src/app/services/env.service';
import { Subject } from 'rxjs';
import { SessionsManagerService } from './../../services/sessions-manager-service';
import { SessionState, Session } from './../../models/session';
import { Component, OnInit } from '@angular/core';
import { takeUntil } from 'rxjs/operators';
import { IonRefresher, NavController } from '@ionic/angular';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: 'app-usr-reservations',
  templateUrl: './usr-reservations.page.html',
  styleUrls: ['./usr-reservations.page.scss'],
})
export class UsrReservationsPage implements OnInit {
  state = SessionState;
  stateString=Session.getStateStringByState;
  selectedState = SessionState.ACTIVE;
  filteredSessions=[];
  activeCount = 0;
  reservedCount = 0;
  endedCount = 0;
  refresher:IonRefresher = null;
  constructor(
    private sessionsManagerService:SessionsManagerService,
    private navController:NavController,
    private env:EnvService
  ) {
    
   }
  segmentChanged(event)
  {
    //this.selectedState = event.target.value;
    this.filterSessions();
  }
  setCount(){
    this.activeCount = this.sessionsManagerService.allMySessionsShared.resource().filter(session=>{
      return Session.getState(session) == SessionState.ACTIVE;
    }).length;
    this.reservedCount = this.sessionsManagerService.allMySessionsShared.resource().filter(session=>{
      return Session.getState(session) == SessionState.RESERVED;
    }).length;
    this.endedCount = this.sessionsManagerService.allMySessionsShared.resource().filter(session=>{
      return Session.getState(session) == SessionState.ENDED;
    }).length;
  }
  filterSessions()
  {
    const sessions = this.sessionsManagerService.allMySessionsShared.resource().filter(session=>{
      return Session.getState(session) == this.selectedState;
    });
    this.filteredSessions = [
      { title: null, sessions: sessions, count:sessions.length }
    ];
  }
  ngOnInit() {
    this.sessionsManagerService.allMySessionsShared.getUpdateNotifier(this)
      .subscribe(result=>{
        this.filterSessions();
        this.setCount();
        if(this.refresher)
        {
          this.refresher.complete();
        }
      });
    this.filterSessions();
    this.setCount(); 
    this.sessionsManagerService.allMySessionsShared.request();
  }
  ngOnDestroy(): void {
   
    
  }
  refresh(ev)
  {
    this.refresher = ev.target;
    this.sessionsManagerService.allMySessionsShared.request();
  }
  navigateToPeerChats()
  {
    this.navController.navigateForward(['peer-chats/'+this.env.USER_ROLE_ID]);
  }
}
